import React, { useEffect } from 'react';
import './ErrorMessage.scss';

interface Props {
  onClose: () => void;
}

export const ErrorMessage: React.FC<Props> = ({ onClose }) => {
  useEffect(() => {
    const timeout = setTimeout(() => {
      onClose();
    }, 3000);

    return () => {
      clearTimeout(timeout);
    };
  }, []);

  return (
    <div className="error">❌ No correct query ...</div>
  );
};
