import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import classNames from 'classnames';
import { Chart } from '../Chart';
import { CardStatistics } from '../../types/CardStatistics';
import './Card.scss';
import { actions as cardsActions } from '../../store/reducers/cardsReducer';
import { CardHeader } from './CardHeader';
import { CardFooter } from './CardFooter';

interface Props {
  card: CardStatistics;
}

export const Card: React.FC<Props> = ({ card }) => {
  const { id, temp_C, list } = card;

  const [isFahrenheit, setIsFahrenheit] = useState(false);

  const isCold = temp_C <= 0;

  const dispatch = useDispatch();

  const handleToFahrenheit = () => {
    setIsFahrenheit(true);
  };

  const handleToCelsius = () => {
    setIsFahrenheit(false);
  };

  const deleteCard = (cardId: string) => dispatch(cardsActions.delete(cardId));

  return (
    <div
      className={classNames('card', {
        'card--cold': isCold,
      })}
    >
      <CardHeader card={card} />

      <div className="card__chart">
        <Chart list={list} isCold={isCold} isFahrenheit={isFahrenheit} />
        <div className="card__chart__wrapper" />
      </div>

      <CardFooter
        card={card}
        isFahrenheit={isFahrenheit}
        isCold={isCold}
        onFahrenheit={handleToFahrenheit}
        onCelsius={handleToCelsius}
      />

      <button
        type="button"
        className="card__close"
        onClick={() => {
          deleteCard(id);
        }}
      >
        <div className="card__close__icon">✕</div>
      </button>
    </div>
  );
};
